
  ---
title: 
---
<template>
  <my-form :model="model"
           size="small"
           submit-text=""
           :reset-text="null"
           :on-submit="onSubmit">
    <my-title></my-title>
    <el-row :gutter="20">
      <el-col :span="12">
        <my-input label="" name="email"></my-input>
        <my-input label="" name="name"></my-input>
        <my-input label="" name="info" :props="{type:'textarea',rows:5}"></my-input>
        <my-select label="/" name="country" :loader="optionsLoader"></my-select>
        <my-cascader label="" name="city" :loader="optionsLoader"></my-cascader>
        <my-input label="" name="county"></my-input>
        <my-input label="" name="phone"></my-input>
      </el-col>
      <el-col :span="12">
        <my-form-custom name="avatar">
          <my-description top align="center">
            <my-avatar slot="title" :size="200" :src="model.avatar"></my-avatar>
            <el-upload
              class="avatar-uploader"
              action=""
              :show-file-list="false"
              :before-upload="beforeUpload">
              <el-button icon="el-icon-upload2" size="small"></el-button>
            </el-upload>
          </my-description>
        </my-form-custom>
      </el-col>
    </el-row>
  </my-form>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Button as __Button__,Col as __Col__,Row as __Row__,Upload as __Upload__} from 'element-ui';
     import {MyAvatar as __MyAvatar__,MyCascader as __MyCascader__,MyDescription as __MyDescription__,MyForm as __MyForm__,MyFormCustom as __MyFormCustom__,MyInput as __MyInput__,MySelect as __MySelect__,MyTitle as __MyTitle__} from '$ui';
     
     
     
     import MockForExample from '$my/code/mixin/mock-for-example'

  export default {
    mixins: [MockForExample],
    data() {
      return {
        model: {}
      }
    },
    methods: {
      beforeUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        if (isJPG && isLt2M) {
          // 
          this.mockUpload({
            data: {
              name: file.name
            }
          }).then(res => {
            this.model.avatar = res
          })
        }
        return false

      },
      optionsLoader(model, {name}) {
        return this.getOptions({
          data: {
            name
          }
        })
      },
      onSubmit(model) {
        return this.updateMockForExample({
          data: model
        }).then(r => {
          this.$message.success('')
        })
      }
    },
    created() {
      this.getMockForExample({
        id: '123'
      }).then(res => {
        this.model = res
      })
    }
  }
     __vue__.use(__Button__);
__vue__.use(__Col__);
__vue__.use(__Row__);
__vue__.use(__Upload__)
     __vue__.use(__MyAvatar__);
__vue__.use(__MyCascader__);
__vue__.use(__MyDescription__);
__vue__.use(__MyForm__);
__vue__.use(__MyFormCustom__);
__vue__.use(__MyInput__);
__vue__.use(__MySelect__);
__vue__.use(__MyTitle__)
    
  </script>


  