/**
 * 
 * @module $ui/utils/tree
 */

/**
 * 
 * @param {Object[]} list  id/parentId/children
 * @param {*} [parentId=null] 
 * @param {string} [idKey=id] id
 * @param {string} [parentIdKey=parentId] parentId
 * @return {Array}
 */
export function create(list = [], parentId = null, idKey = 'id', parentIdKey = 'parentId') {
  const temp = Object.create(null), tree = []
  list.forEach(item => {
    temp[item[idKey]] = {...item}
  })
  
  Object.keys(temp).forEach(key => {
    const item = temp[key]
    if (item[parentIdKey] === parentId) {
      tree.push(item)
    } else {
      const parent = temp[item[parentIdKey]]
      if (parent) {
        if (!parent.children) {
          parent.children = []
        }
        parent.children.push(item)
      }
    }
  })
  
  return tree
}

/**
 * 
 * @module $ui/utils/tree
 */

/**
 * 
 * @param {Array|Object} data  {id:1, children:[{id:2}]}
 * @param {Boolean} isFindOne 
 * @param {Function} fn item indexdata true
 * @param {string} [field=children] 
 * @returns {Array|Object} isFindOnetrueObject falseArray
 */
export function find(data = [], isFindOne, fn, field = 'children') {
  let result = []
  data = Array.isArray(data) ? data : [data]
  for (let i = 0, len = data.length; i < len; i++) {
    const item = data[i],
      checked = fn(item, i, data),
      children = item[field]
    if (checked) {
      result.push(item)
      if (isFindOne) break
    }
    if (children) {
      const child = find(children, isFindOne, fn, field)
      if (child) result = result.concat(child)
    }
  }
  return isFindOne ? result[0] || null : result
}


/**
 * 
 * @param {Array|Object} data   {id:1, children:[{id:2}]}
 * @param {Function} fn item indexdata true
 * @param {string} [field=children] 
 * @return {Array} 
 */
export function findPath(data, fn, field = 'children') {
  const path = []
  
  function find(array, parent) {
    parent && path.push(parent)
    for (let i = 0, len = array.length; i < len; i++) {
      const item = array[i],
        checked = fn(item, i, array),
        children = item[field]
      // 
      if (checked) {
        path.push(item)
        return true
      }
      if (children && children.length > 0) {
        // 
        if (find(children, item)) {
          return true
        } else {
          path.pop()
        }
      }
    }
  }
  
  find([].concat(data))
  return path
  
}
